import React, { useCallback, useState } from "react";
import Button from "../../components/Button";
import { putClaimDocuments, uploadClaimDocuments } from "../../lib/AppHelper";
import { inputTypes } from "./inputTypes";

export const HealthDocuments = ({
  step,
  members,
  prevStep,
  setValue,
  onSubmit,
  value,
  state,
  columns,
  onRemove,
}) => {
  const [errors, setErrors] = useState({});

  const errorCheck = useCallback((field) => {
    let error;
    if (!state[field] || state[field].length < 1) {
      error = true;
      setErrors((errors) => {
        return {
          ...errors,
          [field]: "Please Fill this field",
        };
      });
    } else {
      setErrors((errors) => {
        return {
          ...errors,
          [field]: void 0,
        };
      });
    }
    return error;
  });
  const onClick = useCallback(() => {
    let error = false;
    columns.map((column) => {
      const { id } = column;

      error = errorCheck([`doc_${id}`]);
    });
    setTimeout(() => {
      if (!error) {
        setErrors({});
        return onSubmit();
      }
    }, 100);
  }, [errors, columns, state]);
  const handleUpdate = useCallback(
    async (id, file) => {
      await uploadClaimDocuments(id, state.claimId, file).then((res) => {
        if (res.code === 200) {
          setValue({
            [`doc_${id}`]: [
              ...(state[`doc_${id}`] ? state[`doc_${id}`] : []),
              {
                ...res.data.uploaded[0],
                url: res.data.encodedResource,
              },
            ],
          });
        }
      });
    },
    [state]
  );
  const updateDocuments = useCallback(
    async (id, file) => {
      await putClaimDocuments(id, state.claimId, state.subClaimId, file).then(
        (res) => {
          if (res.code === 200) {
            setValue({
              [`doc_${id}`]: res.data.uploaded.map((doc) => {
                return {
                  ...doc.uploaded[0],
                  url: doc.encodedResource,
                };
              }),
            });
          }
        }
      );
    },
    [state]
  );
  return (
    <div>
      <h4 className="mb-20">Hi, Please Upload Required Documents</h4>
      {columns.map((column) => {
        const { type, id } = column;
        let RenderComponent = inputTypes["file"];
        return (
          <RenderComponent
            key={id}
            label={type}
            error={[errors[id]]}
            onRemove={onRemove}
            field={`doc_${id}`}
            attrs={{
              multiple: state.forUpdate ? true : false,
              onChange: (e) => {
                if (state.forUpdate) {
                  updateDocuments(id, e.target.files);
                } else {
                  handleUpdate(id, e.target.files[0]);
                }
              },
              onBlur: (e) => {
                errorCheck([`doc_${id}`]);
              },
              value: state[`doc_${id}`],
            }}
          />
        );
      })}

      <Button
        attrs={{
          //   ref: this.BasicBtn,
          type: "button",
          onClick,
        }}
        options={members}
        size="md"
        className={"mt-5"}
        title={"Continue"}
      />
      <button
        onClick={prevStep}
        className="w-full flex items-center justify-center px-6 py-2 mt-3 border border-transparent text-base rounded-xl text-gray-700 bg-none hover:bg-none active:bg-none focus:outline-none focus:ring-2 focus:ring-primary-200 disabled:text-gray-300 hover:text-primary-500"
      >
        Back
      </button>
    </div>
  );
};
