import React, { useCallback, useEffect, useState } from "react";
import Button from "../../components/Button";
import { ApiGet } from "../../lib/AppHelper";
import { UserCard } from "../UserCard";

export const SelectSubPolicyForm = ({
  step,
  members,
  setStep,
  setValue,
  onSubmit,
  value,
  field,
  state,
  subValue,
  subField,
}) => {
  const [data, setData] = useState([]);
  const [loading, setLoading] = useState(false);
  useEffect(() => {
    if (state.memberId && state.policyId) {
      setLoading(true);
      getSubPolicies();
    }
  }, [state.memberId, state.policyId]);
  const getSubPolicies = async () => {
    await ApiGet("member-subpolicy", {
      member_id: state.memberId,
      policy_id: state.policyId,
    })
      .then((res) => {
        setData(res.data);
      })
      .finally(() => {
        setLoading(false);
      });
  };

  const [error, setError] = useState(null);
  const handleRadioChange = useCallback(
    (event) => {
      const item = event.target.value;
      let checkedValue = Number(item["sub_policy_id"]);
      let subCheckedValue = Number(item["sub_product_id"]);
      let updatedValue = [...value];
      let subUpdatedValue = [...subValue];
      if (updatedValue.indexOf(checkedValue) > -1) {
        updatedValue.splice(updatedValue.indexOf(checkedValue), 1);
        subUpdatedValue.splice(subUpdatedValue.indexOf(subCheckedValue), 1);
        setValue({
          [field]: [...updatedValue],
          [subField]: [...subUpdatedValue],
        });
      } else {
        setValue({
          [field]: [...value, checkedValue],
          [subField]: [...subValue, subCheckedValue],
        });
      }
    },
    [setValue, value]
  );
  const onClick = () => {
    if (value.length) {
      setError(false);
      return onSubmit();
    } else {
      setError("Please select ");
    }
  };
  return loading ? (
    <p>loading...</p>
  ) : data && data.length ? (
    <div>
      <h4>Please select the Sub Policy</h4>
      {data.map((item) => (
        <UserCard
          item={item}
          valueField={"sub_policy_id"}
          labelField={"name"}
          selectedValue={value}
          handleRadioChange={handleRadioChange}
        />
      ))}
      <div style={{ color: "red", marginLeft: 20, marginRight: 20 }}>
        {error}
      </div>

      <Button
        attrs={{
          //   ref: this.BasicBtn,
          type: "button",
          onClick,
        }}
        options={members}
        size="md"
        className={"mt-5"}
        title={"Continue"}
      />
    </div>
  ) : (
    <h4>Member don't have any sub policy. Please avail any subpolicy first</h4>
  );
};
