import React from 'react'
import { connect } from 'react-redux'
import InputLabel from './InputLabel';

export function InputSelect({ loading = false, label = "", labelLink = null, attrs = {}, options = [], requiredMark = "" }) {

  let loading_disabled = loading ? { disabled: true } : {};

  return (
    <div className="mb-5">
      <InputLabel label={label} labelLink={labelLink} requiredMark={requiredMark} />
      <div className="mt-2 flex space-x-4">
        <select className="focus:ring-0 focus:border-primary focus:bg-primary-50 block w-full px-4 text-base border-gray-300 rounded-xl disabled:bg-gray-100"
          {...attrs}
          {...loading_disabled}
        >
          {options.map((option, index) => {
            return <option key={index} value={option.value}>{option.label}</option>
          })}
        </select>
      </div>
    </div>
  )
}

const mapStateToProps = (state) => {
  const { loading } = state
  return { loading: loading }
};

export default connect(mapStateToProps)(props => {
  return <InputSelect {...props} />
})