import React, { useEffect, useState } from "react";
import { Provider, useSelector } from "react-redux";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
} from "react-router-dom";
import { AppStore } from "./lib/AppStore";

import Login from "./screens/Login";
import PrivateRoute from "./lib/PrivateRoute";
import { ApiGet, getData, removeData, setError } from "./lib/AppHelper";
import AppRoutes from "./lib/AppRoutes";
import { useLocation } from "react-router";
import Terms from "./screens/Terms";
import PasswordlessLogin from "./screens/PasswordlessLogin";
import { setResellerToken } from "./helpers/Helper";
export const RouterContext = React.createContext();

const LoginRoute = () => {
  const token = useSelector((state) => state.token);
  if (token) return <Redirect to={{ pathname: "/" }} />;
  return <Login />;
};

const RouterProvider = ({ children }) => {
  const location = useLocation();

  const [route, setRoute] = useState({
    //--> It can be replaced with useRef or localStorage
    to: location.pathname,
    from: location.pathname, //--> previous pathname
  });

  useEffect(() => {
    setRoute((prev) => ({ to: location.pathname, from: prev.to }));
  }, [location]);

  return (
    <RouterContext.Provider value={route}>{children}</RouterContext.Provider>
  );
};

function App() {
  const [loaded, setLoaded] = React.useState(false);
  // const [timeLoaded, setTimeLoaded] = React.useState(false)

  React.useEffect(() => {
    async function bootstrap() {
      const token = await getData("access_token");
      const is_admin = await getData("is_admin");

      if (token) {
        const response = await ApiGet(`${is_admin ? "admin/" : ""}me`);
        setResellerToken(response.data.reseller_id_by_token); 
        if (response.status === "success") {
          AppStore.dispatch({
            type: "SIGN_IN",
            user: response.data,
            token: token,
          });
        } else {
          await removeData("access_token");
          await setError(response.message);
        }
        setLoaded(true);
      } else {
        setLoaded(true);
      }

      /* setTimeout(() => {
        setTimeLoaded(true)
      }, 2000) */
    }

    bootstrap();

    return () => { };
  }, []);

  if (!loaded) return false;

  /*
  <React.Fragment>
    <div className="fixed w-screen flex items-center justify-center h-screen bg-gradient-to-r from-primary-600 to-primary-500 z-50">
      <div className="w-96">
        <AiqaIcon className="w-full text-white" />
      </div>
    </div>
  </React.Fragment>
  */

  return (
    <Provider store={AppStore}>
      <Router>
        <Switch>
          <Route path="/terms">
            <Terms />
          </Route>
          <Route path="/auth/:token">
            <PasswordlessLogin />
          </Route>
          <Route path="/login">
            <LoginRoute />
          </Route>

          <PrivateRoute>
            <RouterProvider>
              <AppRoutes />
            </RouterProvider>
          </PrivateRoute>

          <Route
            path="*"
            render={() => {
              return <Redirect to="/login" />;
            }}
          />
        </Switch>
      </Router>
    </Provider>
  );
}

export default App;
