import React, { Component } from "react";
import _ from "lodash";
import { connect } from "react-redux";
import { AppBackButton } from "../components/AppComponents";
import { withRouter } from "react-router";
import Main from "./Main";
import { SelectSubPolicyForm } from "./StepClaimForm/SelectSubPolicyForm";
import { HospitalDetailForm } from "./StepClaimForm/HospitalDetailForm";
import { HospitalizationReasonForm } from "./StepClaimForm/HospitalizationReasonForm";
import { BankDetailsForm } from "./StepClaimForm/BankDetailsForm";
import { HealthDocuments } from "./StepClaimForm/HealthDocuments";
import {
  ApiGet,
  ApiPostClientId,
  ApiPutClientId,
  getApiClaim,
  getResellerId,
  removeUploadedFile,
  setError,
  submitClaim,
} from "../lib/AppHelper";
import { SubmittedForm } from "./StepClaimForm/SubmittedForm";
import { AppStore } from "../lib/AppStore";
import { ArrowLeftIcon } from "@heroicons/react/outline";
// var axios = require("axios");

export class Claim extends Component {
  constructor(props) {
    super(props);
    const {
      policyId,
      memberId,
      claimId = null,
      subClaimId,
    } = this.props.match.params || {};
    this.state = {
      step: 1,
      subPolicyList: [],
      subPolicyIds: [],
      params: {},
      errors: {},
      policyId,
      memberId,
      resellerId: getResellerId(),
      hospitalId: null,
      hospitalName: null,
      reason: null,
      claimStartDate: null,
      claimEndDate: null,
      amount: null,
      bankName: null,
      accountName: null,
      accountNumber: null,
      ifscCode: null,
      docsRequired: [],
      claimId: claimId,
      hospitals: [],
      subClaimId,
    };
  }
  BasicBtn = React.createRef();

  prevStep = () => {
    const { step } = this.state;
    if (step > 1) {
      this.setState({ step: step - 1 });
    }
  };

  // proceed to the next step
  nextStep = () => {
    const { step } = this.state;
    this.setState({ step: step + 1 });
  };
  onRemove = (field, docId, index) => {
    removeUploadedFile(`docs/${docId}`).then((res) => {
      if (res.code === 200) {
        let newData = this.state[field];
        newData.splice(index, 1);
        this.setState({ [field]: newData });
      }
    });
  };

  getClaim = async (id) => {
    AppStore.dispatch({ type: "LOADING", loading: true });

    const {
      policyId,
      memberId,
      claimId = null,
      subClaimId,
    } = this.props.match.params || {};
    const response = await ApiGet(`claim/${id}`);

    // const response = await getApiClaim(`claims/${id}/sub-claims/${subClaimId}`);
    if (response.status === "success") {
      const {
        policy_id,
        status,
        start_date,
        end_date,
        total_claim_amount,
        claim_reason,
        account_holder_name,
        account_number,
        ifsc_code,
        bank_name,
        insured_member_id,
        hospital_id,
        hospital_name,
        claim_id,
      } = response.data;
      let jsonofdocs = {};
      if (response.data.documents && response.data.documents.length) {
        jsonofdocs["forUpdate"] = true;
        response.data.documents.map((doc) => {
          const data = jsonofdocs[`doc_${doc.documnet_id_type}`] || [];
          jsonofdocs[`doc_${doc.documnet_id_type}`] = [...data, { ...doc }];
        });
      }
      this.setState({
        step: 2,
        params: {},
        errors: {},
        policyId: policy_id,
        memberId: insured_member_id,
        resellerId: getResellerId(),
        hospitalId: String(hospital_id),
        hospitalName: String(hospital_name),
        reason: claim_reason,
        claimStartDate: start_date,
        claimEndDate: end_date,
        amount: total_claim_amount && Number(total_claim_amount),
        bankName: bank_name,
        accountName: account_holder_name,
        accountNumber: account_number,
        ifscCode: ifsc_code,
        docsRequired: [],
        claimId: claim_id,
        ...jsonofdocs,
      });
      AppStore.dispatch({ type: "LOADING", loading: false });
    } else {
      await setError(response.message);
    }
    AppStore.dispatch({ type: "LOADING", loading: false });
  };
  componentDidMount() {
    const {
      policyId,
      memberId,
      claimId = null,
      subClaimId,
    } = this.props.match.params || {};
    if (claimId) {
      this.getClaim(claimId);
    }
    this.getHospitalData();
  }
  getHospitalData = async () => {
    const res = await ApiGet("hospitals");
    this.setState({ hospitals: res.data });
  };
  submitStepForm = async () => {
    AppStore.dispatch({ type: "LOADING", loading: true });
    const { claimId, subClaimId } = this.props.match.params || {};
    const {
      policyId,
      memberId,
      amount,
      reason,
      hospitalId,
      hospitalName,
      accountName,
      accountNumber,
      ifscCode,
      claimEndDate,
      claimStartDate,
      subPolicyList,
      resellerId,
      bankName,
    } = this.state;

    const data = {
      policyId,
      memberId,
      amount,
      reason,
      hospitalId,
      hospitalName,
      accountName,
      accountNumber,
      ifscCode,
      claimEndDate,
      claimStartDate,
      subPolicyList,
      resellerId,
      bankName,
    };
    const claimUpdateApi = `claims/${claimId}/sub-claims/${subClaimId}`;
    if (claimId) {
      await ApiPutClientId(claimUpdateApi, data)
        .then((res) => {
          if (res.code === 200) {
            this.setState({
              claimId: res.data.claimId,
              docsRequired: res.data.docsRequired,
            });
            this.nextStep();
          }
          else if (res.code === 400) {
            setError(res.message);
          }

        })
        .finally(() => {
          AppStore.dispatch({ type: "LOADING", loading: false });
        });
    } else {
      await ApiPostClientId("claims", data)
        .then((res) => {
          if (res.code === 200) {
            this.setState({
              claimId: res.data.claimId,
              docsRequired: res.data.docsRequired,
            });
            this.nextStep();
          }
          else if (res.code === 400) {
            setError(res.message);
          }
        })
        .finally(() => {
          AppStore.dispatch({ type: "LOADING", loading: false });
        });
    }
  };

  submitClaimForm = () => {
    AppStore.dispatch({ type: "LOADING", loading: true });

    submitClaim(
      `claims/${this.state.claimId}/submit?resellerId=${getResellerId()}`
    )
      .then((res) => {
        if (res.code === 200) {
          this.nextStep();
        }
      })
      .finally(() => {
        AppStore.dispatch({ type: "LOADING", loading: false });
      });
  };
  render() {
    const { params, step, errors, subPolicyList, subPolicyIds } = this.state;
    // go back to previous step
    const {
      policyId,
      memberId,
      claimId = null,
      subClaimId,
    } = this.props.match.params || {};
    return (
      <Main MessageRef={this.MessageRef}>
        {(step > 1 && !claimId) || (claimId && step > 2) ? (
          <div
            onClick={this.prevStep}
            className="flex items-center text-primary pb-3"
            style={{ cursor: "pointer" }}
          >
            <ArrowLeftIcon width={17} className="mr-3" />
            <p>{"Back"}</p>
          </div>
        ) : (
          <AppBackButton to="/">Back to Dashboard</AppBackButton>
        )}
        <div className="w-full px-6 max-w-md md:flex-grow-0 flex-1 flex flex-col justify-between space-y-6">
          {step === 1 ? (
            <SelectSubPolicyForm
              key="1"
              onSubmit={this.nextStep}
              setValue={(value) => this.setState(value)}
              value={subPolicyList}
              subValue={subPolicyIds}
              field={"subPolicyList"}
              subField={"subPolicyIds"}
              state={this.state}
            />
          ) : (
            false
          )}

          {step === 2 ? (
            <HospitalDetailForm
              key="2"
              onSubmit={this.nextStep}
              setValue={(value) => this.setState(value)}
              state={this.state}
              prevStep={this.prevStep}
            />
          ) : (
            false
          )}

          {step === 3 ? (
            <HospitalizationReasonForm
              key="3"
              onSubmit={this.nextStep}
              setValue={(value) => this.setState(value)}
              state={this.state}
              prevStep={this.prevStep}
            />
          ) : (
            false
          )}

          {step === 4 ? (
            <BankDetailsForm
              key="4"
              onSubmit={this.submitStepForm}
              setValue={(value) => this.setState(value)}
              state={this.state}
              prevStep={this.prevStep}
            />
          ) : (
            false
          )}

          {step === 5 ? (
            <HealthDocuments
              key="5"
              onSubmit={this.submitClaimForm}
              setValue={(value) => this.setState(value)}
              state={this.state}
              prevStep={this.prevStep}
              columns={this.state.docsRequired}
              onRemove={this.onRemove}
              forUpdate={subClaimId}
            />
          ) : (
            false
          )}
          {step === 6 ? (
            <SubmittedForm
              onSubmit={this.nextStep}
              setValue={(value) => this.setState(value)}
              state={this.state}
              value={subPolicyIds}
              prevStep={this.prevStep}
              columns={this.state.docsRequired}
            />
          ) : (
            false
          )}
        </div>
      </Main>
    );
  }
}

const mapStateToProps = (state) => {
  const { loading } = state;
  return {
    loading: loading,
  };
};

const ClaimConnect = connect(mapStateToProps)((props) => {
  return <Claim {...props} />;
});

export default withRouter(ClaimConnect);
