import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import {
  AppBackButton,
} from "../components/AppComponents";
import { ApiGet } from "../lib/AppHelper";
import { AppStore } from "../lib/AppStore";
import Main from "./Main";
import PaginationData from "./admin/components/PaginationData";
import { Search } from "./Search";

export const Leads = ({ loading }) => {
  const MessageRef = React.useRef();
  const [subscriptions, setSubscriptions] = useState([]);
  const [filterValues, setFilterValues] = useState({});
  // useEffect(() => {
  //   getLeads();
  // }, [filterValues]);

  const getLeads = async () => {
    AppStore.dispatch({ type: "LOADING", loading: true });
    const response = await ApiGet("leads", filterValues);
    if (response.status === "success") {
      setSubscriptions(response.data);
    } else {
      MessageRef.current.show(response.message);
    }

    AppStore.dispatch({ type: "LOADING", loading: false });
  };
  return (
    <Main MessageRef={MessageRef}>
      <div className="w-full flex md:flex-row flex-col">
        <div className="w-96">
          <AppBackButton to="/">Back to Dashboard</AppBackButton>
        </div>
        <div className="w-96 w-full">
          <Search setValue={setFilterValues} filterValues={filterValues} />
        </div>
      </div>

      <div className="w-full flex flex-col justify-between space-y-4">
        <div>
          <div className="container my-10 mx-auto px-1 md:px-12">
            <div className="flex flex-wrap -mx-1 lg:-mx-4">
              {/* <PaginationData data={subscriptions} />  */}
              <PaginationData
                request={ApiGet}
                endPoint={"leads"}
                filterValues={filterValues}
              />
            </div>
          </div>
        </div>
      </div>
    </Main>
  );
};

const mapStateToProps = (state) => {
  const { loading } = state;
  return { loading: loading };
};

const LeadsConnect = connect(mapStateToProps)((props) => {
  return <Leads {...props} />;
});

export default withRouter(LeadsConnect);
