import moment from "moment";
import React, { useCallback, useState } from "react";
import Button from "../../components/Button";
import { inputTypes } from "./inputTypes";
const columns = [
  {
    field: "reason",
    label: "Enter Reason",
  },
  {
    field: "claimStartDate",
    label: "Date of Hospitalization",
    type: "date",
  },
  {
    field: "claimEndDate",
    label: "Date of discharge",
    type: "date",
    disabledDateFunc: (date, state) => {
      if (date > new Date() || date < new Date(state.claimStartDate)) {
        return true;
      }
    },
  },
  {
    field: "amount",
    label: "Bill amount",
    type: "number",
    max: 2000000,
  },
];

export const HospitalizationReasonForm = ({
  step,
  members,
  prevStep,
  setValue,
  onSubmit,
  value,
  state,
}) => {
  const [errors, setErrors] = useState({});
  const errorCheck = useCallback(
    (field) => {
      let error;
      if (!state[field] || (state[field] && state[field].length < 1)) {
        error = true;
        setErrors((errors) => {
          return {
            ...errors,
            [field]: "Please Fill this field",
          };
        });
      } else {
        setErrors((errors) => {
          return {
            ...errors,
            [field]: void 0,
          };
        });
      }
      return error;
    },
    [state]
  );
  const onClick = useCallback(() => {
    let error = false;
    columns.map((column) => {
      const { field } = column;
      error = errorCheck(field, error);
    });
    setTimeout(() => {
      if (!error) {
        setErrors({});
        return onSubmit();
      }
    }, 100);
  }, [errors, columns]);
  return (
    <div>
      <h4 className="mb-20">
        Hi, Please enter the Reason for hospitiliazation
      </h4>
      {columns.map((column, index) => {
        const { label, field, type = "text", max, ...rest } = column;
        let RenderComponent = inputTypes[type];
        return (
          <RenderComponent
            key={index}
            label={label}
            error={[errors[field]]}
            state={state}
            setValue={(date) => {
              setValue({ [field]: moment(date).format("YYYY-MM-DD") });
            }}
            attrs={{
              onChange: (e) => {
                if (field === "amount" && e.target.value > max) {
                  setErrors((errors) => {
                    return {
                      ...errors,
                      [field]: `Amount should be less than ${max}`,
                    };
                  });
                } else {
                  setValue({ [field]: e.target.value });
                }
              },
              onBlur: (e) => {
                errorCheck(field);
              },
              value: state[field],
            }}
            {...rest}
          />
        );
      })}

      <Button
        attrs={{
          //   ref: this.BasicBtn,
          type: "button",
          onClick,
        }}
        options={members}
        size="md"
        className={"mt-5"}
        title={"Continue"}
      />
      <button
        onClick={prevStep}
        className="w-full flex items-center justify-center px-6 py-2 mt-3 border border-transparent text-base rounded-xl text-gray-700 bg-none hover:bg-none active:bg-none focus:outline-none focus:ring-2 focus:ring-primary-200 disabled:text-gray-300 hover:text-primary-500"
      >
        Back
      </button>
    </div>
  );
};
