import React, { Component } from "react";
import { Link, withRouter } from "react-router-dom";
import { AppBackButton } from "../../components/AppComponents";
import Main from "../Main";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import { Grid } from "@mui/material";
import { connect } from "react-redux";

export class Report extends Component {
  state = {
    permissions: false,
  };
  render() {
    const { user } = this.props;
    const { permissions } = user;
    return (
      <Main MessageRef={this.MessageRef}>
        <div className="grid md:grid-cols-2 gap-2">
          <div>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
          </div>
        </div>
        <Grid container spacing={2}>
          <Grid item xs={4}>
            <Link to="/reports/sales">
              <Card
                style={{
                  borderRadius: "10px",
                  height: "100px",
                  border: "1px solid red",
                }}
              >
                <CardContent style={{ textAlign: "center", marginTop: "20px" }}>
                  <p style={{ fontSize: 18 }}>Sales</p>
                </CardContent>
              </Card>
            </Link>
          </Grid>
          <Grid item xs={4}>
            <Link to="/reports/emp-sales">
              <Card
                style={{
                  borderRadius: "10px",
                  height: "100px",
                  border: "1px solid red",
                }}
              >
                <CardContent style={{ textAlign: "center", marginTop: "20px" }}>
                  <p style={{ fontSize: 18 }}>Employees Performance</p>
                </CardContent>
              </Card>
            </Link>
          </Grid>
          {permissions && permissions.admin_dashboard ? (
            <Grid item xs={4}>
              <Link to="/reports/reseller-sales">
                <Card
                  style={{
                    borderRadius: "10px",
                    height: "100px",
                    border: "1px solid red",
                  }}
                >
                  <CardContent
                    style={{ textAlign: "center", marginTop: "20px" }}
                  >
                    <p style={{ fontSize: 18 }}>Dashboard</p>
                  </CardContent>
                </Card>
              </Link>
            </Grid>
          ) : (
            false
          )}
          {permissions && permissions.commission_report_view ? (
            <Grid item xs={4}>
              <Link to="/reports/comissions">
                <Card
                  style={{
                    borderRadius: "10px",
                    height: "100px",
                    border: "1px solid red",
                  }}
                >
                  <CardContent
                    style={{ textAlign: "center", marginTop: "20px" }}
                  >
                    <p style={{ fontSize: 18 }}>Commissions</p>
                  </CardContent>
                </Card>
              </Link>
            </Grid>
          ) : (
            false
          )}
        </Grid>
      </Main>
    );
  }
}

const mapStateToProps = (state) => {
  const { loading, user } = state;
  return { loading: loading, user: user };
};

const ReportConnect = connect(mapStateToProps)((props) => {
  return <Report {...props} />;
});

export default withRouter(ReportConnect);
