import React, { memo, useEffect, useState } from "react";
import "rsuite-table/lib/less/index.less"; // or 'rsuite-table/dist/css/rsuite-table.css'
import { Pagination } from "rsuite";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import moment from "moment";
import { Link } from "react-router-dom";
import { faPhoneSquare } from "@fortawesome/free-solid-svg-icons";
import { AppStore } from "../../../lib/AppStore";
const calculatedHeight = window.innerHeight - 230; // 230 is by appHeader - 60, appfooter - 25, paginationContainer - 60, paddingTop-15, paddingBottom-15, buttons - 30
const initialRowHeight = 46;
const initialHeaderHeight = 40;
const calculatedLimit = (calculatedHeight - initialHeaderHeight) / initialRowHeight;
const PaginationData = memo(
  ({
    request,
    data,
    limit = 15,
    pagination = true,
    filterValues,
    loading,
    endPoint,
    extraData,
    renderItem,
  }) => {
    if (limit < 1) {
      limit = 1;
    }
    const [page, setPage] = useState(1);
    const [totalCount, setTotalCount] = useState(0);
    const [tableData, setTableData] = useState(data);
    const [listLoading, setLoading] = useState(false);
    useEffect(() => {
      const start = limit * (page - 1);

      if (!data) {
        (async () => {
          try {
            AppStore.dispatch({ type: "LOADING", loading: true });
            const res = await request(endPoint, {
              start,
              limit,
              page: filterValues.loan_code ? 1 : page,
              ...filterValues,
            });
            if (res.data.total) {
              setTotalCount(res.data.total);
            }
            if (res.data.results) {
              setTableData(res.data.results);
            } else {
              setTableData(res.data);
            }
          } catch (err) {
          } finally {
            AppStore.dispatch({ type: "LOADING", loading: false });
          }
        })();
      } else {
        const newData = [...data];
        const update = newData.slice(start, limit * page);
        setTotalCount(data.length);
        setTableData(update);
      }
    }, [setTableData, setTotalCount, request, page, data, filterValues]);
    return (
      <div style={{ display: "flex", flexDirection: "column", flex: 1 }}>
        {tableData && tableData.length === 0 ? (
          listLoading ? (
            <p>loading...</p>
          ) : (
            <>
              <div className="text-center">
                <p className="text-xl">You do not have any item</p>
              </div>
            </>
          )
        ) : (
          <div className="flex flex-wrap">
            {tableData &&
              tableData.map((item, index) => {
                if (renderItem) {
                  return renderItem(item);
                }
                return (
                  <div className="my-1 px-1 w-80 md:w-1/2 lg:my-4 lg:px-4 lg:w-1/3 ">
                    <article className="overflow-hidden rounded-lg shadow-lg" key={index}>
                      <Link to={`/subscription/${item.application_id}`}>
                        <header className="flex items-center justify-between leading-tight p-2 md:p-4">
                          <h1 className="text-lg font-bold capitalize">
                            {item.customer_name
                              ? item.customer_name
                              : item.name}
                          </h1>
                          <p className="text-grey-darker text-sm">
                            {moment(item.start_date ? item.start_date : item.application_date).format("DD, MMM YYYY")}
                          </p>
                        </header>
                      </Link>
                      <body className="flex items-center justify-between leading-tight p-2 md:p-4">
                        <Link to={`/subscription/${item.application_id}`}>
                          <p className="text-sm text-clip overflow-hidden ...">
                            {item.product_name}
                          </p>
                        </Link>
                        <a
                          className="flex items-center no-underline text-black"
                          href={`tel:+91${item.mobile}`}
                        >
                          <p className="ml-2 text-sm">
                            <FontAwesomeIcon
                              icon={faPhoneSquare}
                              className="text-primary"
                            /> {item.mobile}
                          </p>
                        </a>
                      </body>
                    </article>
                  </div>
                );
              })}
          </div>
        )}
        {pagination && tableData && tableData.length > 0 ? (
          <div style={{ padding: 20 }}>
            <Pagination
              prev
              next
              first
              last
              ellipsis
              boundaryLinks
              maxButtons={2}
              size="xs"
              layout={["total", "-", "pager", "skip"]}
              total={totalCount}
              limit={limit}
              activePage={page}
              onChangePage={setPage}
            />
          </div>
        ) : null}
      </div>
    );
  }
);

export default PaginationData;
