import React from "react";
import InputGroup from "../../components/form/InputGroup";

const NumberInput = ({
  label,
  error,
  setValue,
  field,
  onBlur,
  value,
  attrs,
}) => {
  attrs = {
    ...attrs,
    type: "number",
  };
  return <InputGroup label={label} error={error} attrs={attrs} />;
};
export default NumberInput;
