import React, { Component, useEffect, useState } from "react";
import { connect } from "react-redux";
import { Link, withRouter, useParams } from "react-router-dom";
import {
  AppBackButton,
  AppHeading,
  MemberIconWithName,
} from "../components/AppComponents";
import { ApiGet, setError, downloadPDFFile, getData } from "../lib/AppHelper";
import { AppStore } from "../lib/AppStore";
import Payment from "./application/Payment";
import VpaMandate from "./application/VpaMandate";
import Main from "./Main";
import { Panel, PanelGroup } from "rsuite";
import * as moment from "moment";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faInfoCircle } from "@fortawesome/free-solid-svg-icons";

const ExpandableComponent = ({ member, policyId }) => {
  const [expanded, setExpanded] = useState(false);
  const [permissions, setPermission] = useState();

  useEffect(() => {
    (async () => {
      const per = await getData("permissions");
      if (per) {
        setPermission(per);
      }
    })();
  }, []);

  return (
    <Panel
      key={member.id}
      header={
        <MemberIconWithName
          member={member}
          setExpanded={setExpanded}
          expanded={expanded}
          policyId={policyId}
          claimAdd={permissions && permissions.claim_add ? true : false}
        />
      }
      expanded={expanded}
      id={"panel" + member.id}
      defaultexpanded="false"
    >
      {expanded ? "" : null}
    </Panel>
  );
};

export class Application extends Component {
  MessageRef = React.createRef();
  state = {
    application: null,
  };

  render() {
    const { application } = this.state;
    if (application === null) return false;
    const { loading } = this.props;

    return (
      <Main MessageRef={this.MessageRef}>
        <div className="flex flex-row">
          <div>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
          </div>
          <div
            style={{ float: "right", marginLeft: "auto", marginRight: "15px" }}
          >
            {application.product_reseller &&
              application.product_reseller.invoice_on_reg === "Y" ? (
              <button
                className={`w-full shadow whitespace-nowrap flex items-center justify-center ml-2 px-4 py-2 border border-transparent text-"base rounded-xl ${false
                  ? `border-primary text-primary bg-primary-50 hover:text-white hover:bg-primary active:bg-primary-700`
                  : `text-white bg-primary hover:bg-primary-700 active:bg-primary-800`
                  } focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500 disabled:bg-gray-200 disabled:text-gray-400`}
                onClick={() => this.generatePdf(this.props.match.params.appId)}
              >
                Download Invoice
              </button>
            ) : (
              ""
            )}
          </div>
        </div>
        <div className="flex flex-col md:flex-row space-x-0 md:space-x-5 space-y-5 md:space-y-0 px-2 space-y-6 customer-detail">
          <div className="flex-auto">
            {application &&
              !application.insured_member_id &&
              application.status !== 1 ? (
              <>
                {application.product_type === "mutual_aid" ? (
                  <VpaMandate
                    onSuccess={this.onSuccess}
                    onError={this.onError}
                    MessageRef={this.MessageRef}
                    application={application}
                  />
                ) : (
                  <Payment
                    onSuccess={this.onSuccess}
                    onError={this.onError}
                    MessageRef={this.MessageRef}
                    application={application}
                  />
                )}
              </>
            ) : (
              <>
                <AppHeading>{application.customer_name}</AppHeading>
                <h6 className="mb-4 pt-6 text-md">Your medical card</h6>

                <div className="bg-primary flip-front p-4 text-white rounded-lg member-card w-96 max-w-full flex flex-col justify-between">
                  <Link to={`/product/${application.product_id}`}>
                    <div>
                      <div
                        style={{
                          display: "flex",
                          justifyContent: "space-between",
                        }}
                      >
                        <p className="text-lg pb-8">
                          {application.product_name}
                        </p>
                        <FontAwesomeIcon icon={faInfoCircle} />
                      </div>
                      {/* <p className="font-thin">{application.member_since.length} family members</p> */}

                      <div className="flex space-x-20">
                        <div
                          className="text-right tracking-widest"
                          style={{ letterSpacing: "4px" }}
                        >
                          {application.insured_member_id}
                        </div>
                      </div>

                      <div className="flex space-x-16 pt-5">
                        <div>
                          <p className="text-sm text-primary-200 mb-1">
                            Activated on
                          </p>
                          <p className="text-sm">
                            {moment(application.start_date).format(
                              "DD MMMM YYYY"
                            )}
                          </p>
                        </div>
                        <div>
                          <p className="text-sm text-primary-200 mb-1">
                            Valid till
                          </p>
                          {/* <p className="text-sm">{application.policy_details.expire_on}</p> */}
                          <p className="text-sm">
                            {moment(application.end_date).format(
                              "DD MMMM YYYY"
                            )}
                          </p>
                        </div>
                      </div>
                    </div>
                  </Link>
                </div>

                <AppHeading className="mt-10 mb-5">Covered Members</AppHeading>

                <PanelGroup accordion bordered defaultexpanded="false">
                  {application.members.map((member, index) => {
                    return (
                      <ExpandableComponent
                        member={member}
                        policyId={application.policy_id}
                        key={index}
                      />
                    );
                  })}
                </PanelGroup>
              </>
            )}
          </div>
        </div>
      </Main>
    );
  }
  async componentDidMount() {
    // await AppStore.dispatch({ type: "LOADING", loading: true });
    // console.log("loading");
    const id = this.props.match.params.appId;
    await this.getApplication(id);
  }

  getApplication = async (id) => {
    AppStore.dispatch({ type: "LOADING", loading: true });

    const response = await ApiGet(`subscription/${id}`);
    if (response.status === "success") {
      this.setState({ application: response.data });
    } else {
      await setError(response.message);
      this.props.history.push("/");
    }
    AppStore.dispatch({ type: "LOADING", loading: false });
  };

  async generatePdf(appId) {
    AppStore.dispatch({ type: "LOADING", loading: true });
    // const response2 = await ApiGet(`invoice/${appId}`);
    const response2 = await downloadPDFFile(`invoice/${appId}`);
    AppStore.dispatch({ type: "LOADING", loading: false });
  }

  onError = (error) => {
    setError(error, () => {
      this.props.history.replace("/subscriptions");
    });
  };

  onSuccess = async (message) => {
    await this.getApplication(this.state.application.id);
    this.MessageRef.current?.show(message, "success");
  };
}

const mapStateToProps = (state) => {
  const { loading } = state;
  return { loading: loading };
};

const ApplicationConnect = connect(mapStateToProps)((props) => {
  return <Application {...props} />;
});

export default withRouter(ApplicationConnect);
