import React, { useCallback, useState } from "react";
import Button from "../../components/Button";
import { ApiGet } from "../../lib/AppHelper";
import { inputTypes } from "./inputTypes";

const columns = [
  {
    field: "hospitalId",
    label: "Name of the Hospital",
    type: "dropdown",
    // request: ApiGet,
    visible: true,
    labelField: "name",
    valueField: "id",
  }, {
    field: "hospitalName",
    type: "text",
    placeholder: "Enter hospital name",
    visible: ({ values }) => {
      return values.hospitalId === '6666' ? true : false
    },
  },
];

export const HospitalDetailForm = ({
  step,
  members,
  prevStep,
  setValue,
  onSubmit,
  value,
  state,
}) => {
  const [errors, setErrors] = useState({});
  const errorCheck = useCallback(
    (field, visible) => {
      let error;
      if (typeof visible === 'function') {
        visible = visible({ values: state })
      }
      if (visible) {
        if (!state[field] || (state[field] && state[field].length < 1)) {
          error = true;
          setErrors((errors) => {
            return {
              ...errors,
              [field]: "Please Fill this field",
            };
          });
        } else {
          setErrors((errors) => {
            return {
              ...errors,
              [field]: void 0,
            };
          });
        }
      } else {
        setErrors((errors) => {
          return {
            ...errors,
            [field]: void 0,
          };
        });
      }
      return error;
    },
    [state]
  );
  const onClick = useCallback(() => {
    let error = false;
    columns.map((column) => {
      const { field, visible = true } = column;
      error = errorCheck(field, visible);
    });
    setTimeout(() => {
      if (!error) {
        setErrors({});
        return onSubmit();
      }
    }, 100);
  }, [errors, state]);
  return (
    <div>
      <h4 className="mb-20">Hi, Please enter the Hospital name and Location</h4>
      {columns.map((column) => {
        let { label, field, type = "dropdown", data, visible, placeholder, ...rest } = column;
        let RenderComponent = inputTypes[type];
        if (typeof visible === 'function') {
          visible = visible({ values: state })
        }
        if (!visible) {
          return null
        }
        return (
          <RenderComponent
            label={label}
            key="1"
            items={data}
            placeholder={placeholder}
            error={[errors[field]]}
            setValue={setValue}
            field={field}
            {...rest}
            options={state.hospitals}
            attrs={{
              onChange: (e) => {
                setValue({ [field]: e.target.value });
              },
              onBlur: (e) => {
                errorCheck(field);
              },
              value: state[field],
            }}
          />);
      })
      }

      <Button
        attrs={{
          //   ref: this.BasicBtn,
          type: "button",
          onClick,
        }}
        options={members}
        size="md"
        className={"mt-5"}
        title={"Continue"}
      />
      <button
        onClick={prevStep}
        className="w-full flex items-center justify-center px-6 py-2 mt-3 border border-transparent text-base rounded-xl text-gray-700 bg-none hover:bg-none active:bg-none focus:outline-none focus:ring-2 focus:ring-primary-200 disabled:text-gray-300 hover:text-primary-500"
      >
        Back
      </button>
    </div >
  );
};
