import React, { Component } from "react";
import { connect } from "react-redux";
import { Link, withRouter } from "react-router-dom";
import { AppCard, AppHeading, AppRow } from "../components/AppComponents";
import Button from "../components/Button";
import ProductsList from "../components/home/ProductsList";
import { AppStore } from "../lib/AppStore";
import Main from "./Main";
import insurance from "./../insurance.svg";
import shield from "./../shield.svg";
import { AiqaIcon } from "../components/AppIcons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faFileArchive,
  faRupeeSign,
  faUserAstronaut,
  faUserFriends,
  faUserMd,
} from "@fortawesome/free-solid-svg-icons";
import { ApiGet } from "../lib/AppHelper";

export class Home extends Component {
  MessageRef = React.createRef();
  state = {
    counts: {},
  };

  render() {
    const { user } = this.props;
    const { is_secured_login } = user;
    const { permissions } = user;
    const { counts } = this.state;

    return (
      <Main MessageRef={this.MessageRef}>
        <AppRow>
          <div className="flex-auto">
            <div className="max-w-3xl">
              <AppRow>
                <div className="flex-1 pt-10">
                  <AppHeading className="mb-6">Hello,</AppHeading>

                  <AppCard title={false} className="mb-6">
                    <div className="flex justify-between">
                      <img alt="" src={insurance} />
                      <div className="flex flex-col justify-between">
                        <AiqaIcon size={30} className="text-primary" />

                        {permissions && permissions.application_add ? (
                          <Link to="/subscribe">
                            <Button
                              size="sm"
                              display="inline-block"
                              title="Add New Member"
                              className="px-6"
                            />
                          </Link>
                        ) : (
                          false
                        )}
                      </div>
                      <img alt="" src={shield} />
                    </div>
                  </AppCard>
                </div>
              </AppRow>
              <div className="grid md:grid-cols-3 grid-cols-2 gap-4">
                <div className="col-span-1">
                  <Link to="/subscriptions">
                    <AppCard className="cursor-pointer" title="Active Members">
                      <AppHeading color="primary">
                        {/* <FontAwesomeIcon icon={faUserAstronaut} /> {user.active_subscription} */}
                        <FontAwesomeIcon icon={faUserAstronaut} />
                        {counts.sol_pol_amont || 0}
                      </AppHeading>
                    </AppCard>
                  </Link>
                </div>

                {permissions && permissions.application_add ? (
                  <div className="col-span-1">
                    <Link to="/leads">
                      <AppCard className="cursor-pointer" title="Unpaid">
                        <AppHeading color="primary">
                          <FontAwesomeIcon icon={faUserAstronaut} />{" "}
                          {counts.unsold_pol_cnt || 0}
                        </AppHeading>
                      </AppCard>
                    </Link>
                  </div>
                ) : (
                  ""
                )}

                {is_secured_login == "Y" ? (
                  <div>
                    <AppCard className="cursor-pointer" title="Total Sales">
                      <AppHeading color="primary">
                        <FontAwesomeIcon icon={faRupeeSign} />
                        {counts.sales_amont || 0}
                      </AppHeading>
                    </AppCard>
                  </div>
                ) : (
                  false
                )}
              </div>

              {is_secured_login == "Y" ? (
                <>
                  <AppHeading className="mb-6 mt-6" size="xl" font="normal">
                    Our Products
                  </AppHeading>
                  <ProductsList />
                </>
              ) : (
                false
              )}
            </div>
          </div>
          {is_secured_login == "Y" ? (
            <div className="flex-2">
              <div className="grid md:grid-cols-1 grid-cols-2 gap-2">
                {permissions && permissions.reseller_add ? (
                  <Link to="/employees">
                    <div
                      className="col-span-1 mt-3 w-400-px"
                      style={{ width: 200 }}
                    >
                      <AppCard className="cursor-pointer" title="View Team">
                        <AppHeading color="primary">
                          <FontAwesomeIcon icon={faUserFriends} />{" "}
                          {counts.no_of_reportee || 0}
                        </AppHeading>
                      </AppCard>
                    </div>{" "}
                  </Link>
                ) : (
                  false
                )}

                {permissions && permissions.claim_view ? (
                  <div className="col-span-1 mt-3" style={{ minWidth: 180 }}>
                    <Link to="/claims">
                      <AppCard className="cursor-pointer" title="Claim">
                        <AppHeading color="primary">
                          <FontAwesomeIcon icon={faUserMd} />{" "}
                          {counts.claim_count || 0}
                        </AppHeading>
                      </AppCard>
                    </Link>
                  </div>
                ) : (
                  false
                )}

                <div className="col-span-1 mt-3" style={{ minWidth: 180 }}>
                  <Link to="/reports">
                    <AppCard className="cursor-pointer" title="Reports">
                      <AppHeading color="primary">
                        <FontAwesomeIcon icon={faFileArchive} />
                      </AppHeading>
                    </AppCard>
                  </Link>
                </div>
              </div>
            </div>
          ) : (
            false
          )}
        </AppRow>
      </Main>
    );
  }

  componentDidMount() {
    AppStore.dispatch({ type: "PROGRESS", progress: 0 });
    this.getCounts();
  }

  getCounts = async () => {
    AppStore.dispatch({ type: "LOADING", loading: true });

    const response = await ApiGet("counts");

    if (response.status === "success") {
      this.setState({ counts: response.data });
    } else {
      this.MessageRef.current.show(response.message);
    }

    AppStore.dispatch({ type: "LOADING", loading: false });
  };
	MessageRef = React.createRef();
	state = {
		counts: {},
	};

	render() {
		const { user } = this.props;
		const { permissions } = user;
		const { counts } = this.state;

		return (
			<Main MessageRef={this.MessageRef}>
				<AppRow>
					<div className="flex-auto">
						<div className="max-w-3xl">
							<AppRow>
								<div className="flex-1 pt-10">
									<AppHeading className="mb-6">Hello, {user.name}</AppHeading>

									<AppCard title={false} className="mb-6">
										<div className="flex justify-between">
											<img alt="" src={insurance} />
											<div className="flex flex-col justify-between">
												<AiqaIcon size={30} className="text-primary" />

												{permissions && permissions.application_add ?
													<Link to="/subscribe">
														<Button
															size="sm"
															display="inline-block"
															title="Add New Member"
															className="px-6"
														/>
													</Link>
													: false}

											</div>
											<img alt="" src={shield} />
										</div>
									</AppCard>
								</div>
							</AppRow>
							<div className="grid md:grid-cols-3 grid-cols-2 gap-4">
								<div className="col-span-1">
									<Link to="/subscriptions">
										<AppCard className="cursor-pointer" title="Active Members">
											<AppHeading color="primary">
												{/* <FontAwesomeIcon icon={faUserAstronaut} /> {user.active_subscription} */}
												<FontAwesomeIcon icon={faUserAstronaut} />{" "}
												{counts.sol_pol_amont || 0}
											</AppHeading>
										</AppCard>
									</Link>
								</div>

								{permissions && permissions.application_add ? (
									<div className="col-span-1">
										<Link to="/leads">
											<AppCard className="cursor-pointer" title="Unpaid">
												<AppHeading color="primary">
													<FontAwesomeIcon icon={faUserAstronaut} />{" "}
													{counts.unsold_pol_cnt || 0}
												</AppHeading>
											</AppCard>
										</Link>
									</div>
								) : (
									""
								)}

								<div>
									<AppCard className="cursor-pointer" title="Total Sales">
										<AppHeading color="primary">
											<FontAwesomeIcon icon={faRupeeSign} />{" "}
											{counts.sales_amont || 0}
										</AppHeading>
									</AppCard>
								</div>

							</div>
							<AppHeading className="mb-6 mt-6" size="xl" font="normal">Our Products</AppHeading>
							<ProductsList />
						</div >
					</div >
					<div className="flex-2">
						<div className="grid md:grid-cols-1 grid-cols-2 gap-2">
							{(permissions && permissions.reseller_add) ?
								<Link to="/employees">
									<div className="col-span-1 mt-3 w-400-px" style={{ width: 200 }}>
										<AppCard className="cursor-pointer" title="View Team">

											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserFriends} /> {counts.no_of_reportee || 0}
											</AppHeading>
										</AppCard>

									</div> </Link> : false}

							{(permissions && permissions.claim_view) ?
								<div className="col-span-1 mt-3" style={{ minWidth: 180 }}>
									<Link to="/claims">
										<AppCard className="cursor-pointer" title="Claim">
											<AppHeading color="primary">
												<FontAwesomeIcon icon={faUserMd} /> {counts.claim_count || 0}</AppHeading>
										</AppCard>
									</Link>
								</div> : false}

							<div className="col-span-1 mt-3" style={{ minWidth: 180 }}>
								<Link to="/reports">
									<AppCard className="cursor-pointer" title="Reports">
										<AppHeading color="primary">
											<FontAwesomeIcon icon={faFileArchive} /></AppHeading>
									</AppCard>
								</Link>
							</div>

						</div>
					</div>
				</AppRow >
			</Main >
		);
	}

	componentDidMount() {
		AppStore.dispatch({ type: "PROGRESS", progress: 0 });
		this.getCounts();
	}

	getCounts = async () => {
		AppStore.dispatch({ type: "LOADING", loading: true });


		const response = await ApiGet("counts");

		if (response.status === "success") {
			this.setState({ counts: response.data });

		} else {
			this.MessageRef.current.show(response.message);
		}

		AppStore.dispatch({ type: "LOADING", loading: false });
	};
}

const mapStateToProps = (state) => {
  const { loading, user } = state;
  return { loading: loading, user: user };
};

const HomeConnect = connect(mapStateToProps)((props) => {
  return <Home {...props} />;
});

export default withRouter(HomeConnect);
