import React, { createContext, useContext } from "react";
import { FiChevronRight } from "react-icons/fi";
import { BiRupee } from "react-icons/bi";
import { Link, useRouteMatch, useHistory } from "react-router-dom";
import { ArrowLeftIcon } from "@heroicons/react/outline";
import {
  DaughterIcon,
  FemaleIcon,
  MaleIcon,
  ParentsIcon,
  RingIcon,
  SonIcon,
} from "./AppIcons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCheckCircle,
  faEye,
  faImage,
  faQuestionCircle,
} from "@fortawesome/free-solid-svg-icons";
import { getApiClaim, ROLE_STATUS_MAPPING } from "../lib/AppHelper";
import { saveAs } from "file-saver";
import { RouterContext } from "../App";
import Button from "./Button";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Typography from "@mui/material/Typography";
import { borderColor } from "@mui/system";

export const AppRow = ({ children, className = "" }) => {
  return (
    <div
      className={[
        "flex flex-wrap flex-col md:flex-row space-x-0 md:space-x-5 space-y-5 md:space-y-0",
        className,
      ].join(" ")}
    >
      {children}
    </div>
  );
};

export const AppHeading = ({
  children,
  color = "black",
  className = "",
  size = "2xl",
  font = "medium",
}) => {
  return (
    <h3
      className={[`text-${size} font-${font} text-${color}`, className].join(
        " "
      )}
    >
      {children}
    </h3>
  );
};

export const AppCard = ({
  children,
  title = "",
  className = "",
  attrs = {},
}) => {
  return (
    <div
      className={[
        `rounded-xl bg-primary-50 border border-primary p-4`,
        className,
      ].join(" ")}
      {...attrs}
    >
      {title ? <h5 className={`text-base mb-2`}>{title}</h5> : false}
      {children}
    </div>
  );
};

export const AppSidebarMenu = ({ children }) => {
  return <div className="flex md:flex-col flex-row">{children}</div>;
};

export const AmountText = ({ amount }) => {
  amount = new Intl.NumberFormat("en-IN", {
    // maximumSignificantDigits: 4,
  }).format(amount);

  return (
    <div className="flex items-center">
      <BiRupee size={17} /> {amount}
    </div>
  );
};

export const AppSidebarMenuItem = ({ children, title = "", link = "/#" }) => {
  const active = useRouteMatch(link);

  return (
    <Link
      to={link}
      className={[
        `flex items-center justify-between px-4 py-6 hover:text-primary-600 focus:outline-none md:border-b last:border-0`,
        active ? "text-primary" : "",
      ].join(" ")}
    >
      <div className="flex-1 flex whitespace-nowrap">
        <div className="inline-block mr-3">{children}</div>
        <p className="text-sm font-semibold">{title}</p>
      </div>
      <span className="md:inline-block hidden">
        <FiChevronRight size={20} />
      </span>
    </Link>
  );
};

export const AppBackButton = ({ to = "/", children }) => {
  const history = useHistory();
  const route = useContext(RouterContext || createContext());
  if (route && route.from == route.to) {
    return (
      <div className="px-6 text-primary mb-6">
        <Link to={"/"}>
          <div className="flex items-center">
            <ArrowLeftIcon width={17} className="mr-3" />
            <p>{"Back to dashboard"}</p>
          </div>
        </Link>
      </div>
    );
  }
  return (
    <div className="px-6 text-primary mb-6">
      <div onClick={history.goBack} style={{ cursor: "pointer" }}>
        <div className="flex items-center">
          <ArrowLeftIcon width={17} className="mr-3" />
          <p>{"Back"}</p>
        </div>
      </div>
    </div>
  );
};

export const AppStatusLabel = ({
  status,
  active = "Active",
  inactive = "Declined",
  pending = "Pending",
  waiting = "Waiting",
}) => {
  switch (status) {
    case 1:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800 mr-2">
          {active}
        </span>
      );
    case 2:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-100 text-red-600  mr-2">
          {inactive}
        </span>
      );
    case 3:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-orange-500 text-white  mr-2">
          {waiting}
        </span>
      );
    case 0:
    default:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-yellow-100 text-yellow-800  mr-2">
          {pending}
        </span>
      );
  }
};

export const AppTeamStatus = ({ status }) => {
  switch (status) {
    case 1:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800 mr-2">
          Active
        </span>
      );
    case 2:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-100 text-red-600 mr-2">
          Inactive
        </span>
      );
    case 3:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-yellow-500 text-white mr-2">
          Waiting
        </span>
      );
    case 0:
    default:
      return (
        <span className="px-3 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-white-800 mr-2">
          Invitation Sent
        </span>
      );
  }
};

export const AppLabel = ({ title, color = "primary" }) => {
  let text_color = "text-primary-800";
  let bg_color = "bg-primary-100";

  switch (color) {
    case "blue":
      text_color = "text-blue-800";
      bg_color = "bg-blue-100";
      break;
    case "indigo":
      text_color = "text-indigo-800";
      bg_color = "bg-indigo-100";
      break;
    case "purple":
      text_color = "text-purple-800";
      bg_color = "bg-purple-100";
      break;
    case "pink":
      text_color = "text-pink-800";
      bg_color = "bg-pink-100";
      break;
    case "orange":
      text_color = "text-orange-800";
      bg_color = "bg-orange-100";
      break;
    case "teal":
      text_color = "text-teal-800";
      bg_color = "bg-teal-100";
      break;
    default:
      text_color = `text-${color}-800`;
      bg_color = `bg-${color}-100`;
      break;
  }

  return (
    <span
      className={`px-3 inline-flex text-xs leading-5 font-semibold mr-2 rounded-full ${text_color} ${bg_color}`}
    >
      {title}
    </span>
  );
};

export const MemberIcon = ({ member, size = 24 }) => {
  if (member.relation === "self" && member.gender === "m")
    return <MaleIcon size={size} />;

  if (member.relation === "self" && member.gender === "f")
    return <FemaleIcon size={size} />;

  if (member.relation === "spouse") return <RingIcon size={size} />;

  if (member.relation === "daughter") return <DaughterIcon size={size} />;

  if (member.relation === "son") return <SonIcon size={size} />;

  if (member.relation === "parent" || member.relation === "spouse_parent")
    return <ParentsIcon size={size} />;

  return false;
};

export const MemberIconWithName = ({
  member,
  size = 30,
  setExpanded,
  expanded,
  policyId,
  claimAdd,
}) => {
  const getHeader = () => {
    let relation = member.relation.toUpperCase();

    if (relation === "SELF" && member.gender === "MALE")
      return (
        <div>
          <MaleIcon size={size} className="absolute" />
          <p className="pl-10 text-medium text-md">{member.name} </p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "HUSBAND" && member.gender === "MALE")
      return (
        <div>
          <MaleIcon size={size} className="absolute" />
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "SELF" && member.gender === "FEMALE")
      return (
        <div>
          <FemaleIcon className="absolute" size={size}>
            {member.name}
          </FemaleIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "SPOUSE")
      return (
        <div>
          <RingIcon size={size} className="absolute"></RingIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "DAUGHTER")
      return (
        <div>
          <DaughterIcon size={size} className="absolute"></DaughterIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "SON")
      return (
        <div>
          <SonIcon size={size} className="absolute"></SonIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );

    if (relation === "PARENT")
      return (
        <div>
          <ParentsIcon size={size} className="absolute"></ParentsIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">{member.relation}</p>
        </div>
      );
    if (relation === "SELF_PARENT_1" || relation === "SELF_PARENT_2")
      return (
        <div>
          <ParentsIcon size={size} className="absolute"></ParentsIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">SELF PARENT</p>
        </div>
      );
    if (relation === "SPOUSE_PARENT")
      return (
        <div>
          <ParentsIcon size={size} className="absolute"></ParentsIcon>
          <p className="pl-10 text-medium text-md">{member.name}</p>
          <p className="pl-10 text-xs">SPOUSE PARENT</p>
        </div>
      );
  };

  return (
    <>
      <div
        onClick={() => {
          setExpanded((expanded) => !expanded);
        }}
        style={{
          flexDirection: "row",
          display: "flex",
          justifyContent: "space-between",
        }}
      >
        {getHeader()}
        <div className="flex justify-center" style={{ alignItems: "center" }}>
          <Link
            to={{
              pathname: `/claim/${member.id}/${policyId}`,
              params: { memberId: member.id, policyId },
            }}
          >
            {claimAdd ? <Button size={"sm"} title="Raise Claim" /> : false}
          </Link>
          <div style={{ width: 10, marginLeft: 10 }}>
            {expanded ? "-" : "+"}
          </div>
        </div>
      </div>
      {expanded ? (
        <div>
          {member.claim.map((item) => {
            return (
              <div key={item.claim_id} style={{ margin: "10px" }}>
                <Card sx={{ borderColor: "error.main" }}>
                  <CardContent>
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Bank Name : {item.bank_name}
                    </Typography>
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Account Holder Name : {item.account_holder_name}
                    </Typography>
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Claim Reason : {item.claim_reason}
                    </Typography>{" "}
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Total Claim Amount : {item.total_claim_amount}
                    </Typography>{" "}
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Payment Status :{" "}
                      {item.payment_status ? item.payment_status : "Not found"}
                    </Typography>
                    <Typography
                      sx={{ fontSize: 14 }}
                      color="text.secondary"
                      gutterBottom
                    >
                      Claim Status :{" "}
                      {item.status ? item.status : "Not found"}
                    </Typography>
                  </CardContent>
                </Card>
              </div>
            );
          })}
        </div>
      ) : (
        ""
      )}
    </>
  );
};

export const Tenor = ({ tenor }) => {
  const tenorSuffix = tenor > 0 ? "s" : "";
  if (tenor == 18) return "1.5 Years";
  else if (tenor == 1) return "1.5 Year";
  else if (tenor == 2) return "2 Years ";
  else return tenor + " Month" + tenorSuffix;

  return false;
};
const convertBase64ToFile = (base64String, fileName, type) => {
  let bstr = atob(base64String);
  let n = bstr.length;
  let uint8Array = new Uint8Array(n);
  while (n--) {
    uint8Array[n] = bstr.charCodeAt(n);
  }
  let file = new File([uint8Array], fileName, { type });
  return file;
};
const downloadBase64Data = (base64String, fileName, type) => {
  try {
    let file = convertBase64ToFile(base64String, fileName, type);
    saveAs(file, fileName);
  } catch (e) {
    console.log("@@@@@@file", e);
  }
};

export const DocumentView = ({ item }) => {
  const getData = async () => {
    getApiClaim(`docs/${item.id}`)
      .then((res) => {
        downloadBase64Data(
          res.data.encodedResource,
          res.data.document.documentName,
          res.data.mimeType
        );
      })
      .catch(() => { });
  };

  return (
    <div className="max-w-sm rounded overflow-hidden shadow-md border-dashed border-2 border-red-200 ">
      <div className="px-6 py-4">
        <span className="text-xl">
          <FontAwesomeIcon icon={faImage} className=""></FontAwesomeIcon>
        </span>
        <div className="mb-2 pt-2 text-gray-500 text-base">
          {item.document_name || item.documentName}
        </div>
        <div
          className="text-xl"
          style={{ cursor: "pointer" }}
          onClick={getData}
        >
          <FontAwesomeIcon icon={faEye}></FontAwesomeIcon>
        </div>
      </div>
    </div>
  );
};

export const ClaimStatus = ({ status }) => {
  let text_color = "text-primary-800";
  let bg_color = "bg-primary-100";

  switch (ROLE_STATUS_MAPPING[status]) {
    case "Queried":
      text_color = "text-blue-800";
      bg_color = "bg-blue-100";
      break;
    case "Rejected":
      text_color = "text-red-800";
      bg_color = "bg-red-100";
      break;
    case "DRAFT":
      text_color = "text-yellow-800";
      bg_color = "bg-yellow-100";
      break;
    case "In Progress":
      text_color = "text-yellow-800";
      bg_color = "bg-yellow-100";
      break;
    case "Approved":
      text_color = "text-green-800";
      bg_color = "bg-green-100";
    case "Settled":
      text_color = "text-white-800";
      bg_color = "bg-green-100";
      break;
    default:
      text_color = `text-primary-800`;
      bg_color = `bg-primary-100`;
      break;
  }

  return (
    <span
      className={`px-3 inline-flex text-xs leading-5 font-semibold mr-2 rounded-full ${text_color} ${bg_color}`}
    >
      {ROLE_STATUS_MAPPING[status] && ROLE_STATUS_MAPPING[status]}
    </span>
  );
};

export const CouponAlert = ({ status, message }) => {
  let className = "text-primary-800";
  let icons = "";

  if (status) {
    className = "text-green-600";
    icons = <FontAwesomeIcon icon={faCheckCircle}></FontAwesomeIcon>;
  } else {
    className = "text-primary-600";
    icons = <FontAwesomeIcon icon={faQuestionCircle}></FontAwesomeIcon>;
  }

  return (
    <>
      <span className={className}>
        {icons} {message}
      </span>
    </>
  );
};
