import React, { Component } from "react";
import { Link } from "react-router-dom";
import { AppRow } from "../../components/AppComponents";
import AdminMain from "./AdminMain";
import {
  RiUserStarFill,
  RiUserAddFill,
  RiUserFollowFill,
  RiUserSettingsFill,
  RiUserFollowLine,
  RiFileUserFill,
} from "react-icons/ri";

const DashboardIcon = ({ title, to, icon, classes = null }) => {
  return (
    <Link
      to={to}
      className={
        classes +
        " ml-0 pl-0 mb-5 border border-primary rounded-xl w-40 h-40 flex justify-center items-center md:justy-hover:bg-primary-50"
      }
    >
      <div>
        <div className="flex justify-center items-center text-primary">
          {icon}
        </div>
        <div className="text-xs text-center uppercase font-bold mt-4">
          {title}
        </div>
      </div>
    </Link>
  );
};

export class AdminDashboard extends Component {
  MessageRef = React.createRef();

  render() {
    return (
      <AdminMain MessageRef={this.MessageRef}>
        <AppRow>
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Manage</p>
                <p>Resellers</p>
              </React.Fragment>
            }
            to="resellers"
            icon={<RiUserStarFill size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Add</p>
                <p>MLM Reseller</p>
              </React.Fragment>
            }
            to="add-mlm"
            icon={<RiUserAddFill size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Add</p>
                <p>MFI Reseller</p>
              </React.Fragment>
            }
            to="add-mfi"
            icon={<RiUserFollowFill size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Add</p>
                <p>Corporate</p>
              </React.Fragment>
            }
            to="add-corporate"
            icon={<RiUserFollowLine size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Reseller's</p>
                <p>Products</p>
              </React.Fragment>
            }
            to="reseller-products"
            icon={<RiUserSettingsFill size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Reseller's</p>
                <p>Report</p>
              </React.Fragment>
            }
            to="reseller-reports"
            icon={<RiFileUserFill size={40} />}
          />
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Reseller's</p>
                <p>Roles</p>
              </React.Fragment>
            }
            to="roles"
            icon={<RiFileUserFill size={40} />}
          />
        </AppRow>
        {/* <AppRow>
          <DashboardIcon
            title={
              <React.Fragment>
                <p>Reseller's</p>
                <p>Roles</p>
              </React.Fragment>
            }
            to="roles"
            icon={<RiFileUserFill size={40} />}
          />
        </AppRow> */}
      </AdminMain>
    );
  }
}

export default AdminDashboard;
